@charset "UTF-8";
:root {
  --color-black: #000;
  --color-white: #fff;
  --color-text: #000;
  --color-accent: #019ad2;
  --color-subtext: #6e6e6e;
  --color-header: #4682b4;
}

@font-face {
  font-family: "Roboto-Bold";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../Roboto-Bold.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto-Medium";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../Roboto-Medium.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

