}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  background-color: var(--color-white);
  font-size: 14px;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

.error {
  border: 2px #ff4019 solid !important;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.section-title {
  font-weight: 700;
  font-size: 28px;
  text-transform: uppercase;
}

.anchor {
  display: block;
  position: relative;
  top: -125px;
  visibility: hidden;
}

@media (max-width: 991px) {
  .anchor {
    top: -75px;
  }
}

.header {
  background-color: transparent;
  padding: 30px 0px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  z-index: 100;
  transition: 0.6s;
}

.header__container {
  width: 1200px;
}

.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 0;
}

.header__top a:hover {
  color: var(--color-accent);
  transition: all 0.3s;
  transform: scale(1.1);
}

.header__social {
  display: flex;
  column-gap: 15px;
}

.header__social svg {
  width: 24px;
  height: 24px;
}

.header__social svg:hover {
  fill: var(--color-accent);
  transition: all 0.3s;
  transform: scale(1.2);
}

.header__tell {
  color: var(--color-text);
}

.header__call {
  padding: 0;
  border: none;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  padding: 5px 12px;
  border: 2px solid var(--color-black);
}

.header__call:hover {
  color: var(--color-accent);
  border: 2px solid var(--color-accent);
  transition: all 0.3s;
}

.logo {
  position: relative;
}

.logo svg {
  height: 50px;
  width: 50px;
}

.logo a:hover {
  transform: scale(1);
}

header.sticky {
  padding: 5px 100px;
  background: #4682b4;
  transition: 0.6s;
}

header.sticky ul li a {
  color: var(--color-white);
}

header.sticky svg {
  fill: #fff;
  transition: 0.6s;
}

header.sticky button {
  color: var(--color-white);
  border-color: var(--color-white);
  transition: 0.6s;
}

@media (max-width: 991px) {
  .header {
    padding: 0;
  }
  .header__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--color-header);
  }
  .header__schedule,
  .header__tell,
  .header__call,
  .header__social {
    display: none;
  }
  header.sticky {
    padding: 0;
    background: transparent;
    transition: 0.6s;
  }
}

nav ul {
  display: flex;
  justify-content: space-between;
  text-align: center;
}

nav ul li a {
  display: block;
  padding: 4px 15px 6px;
  color: var(--color-text);
  font-weight: 600;
  text-transform: uppercase;
}

nav ul li a,
nav ul li a:after,
nav ul li a:before {
  transition: all 0.3s;
}

nav ul li a:hover {
  color: var(--color-accent);
}

nav.header__nav ul li a,
nav.fill ul li a {
  position: relative;
}

nav.header__nav ul li a:after,
nav.fill ul li a:after {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 0%;
  content: ".";
  color: transparent;
  background: var(--color-accent);
  height: 2px;
}

nav.header__nav ul li a:hover:after {
  width: 100%;
}

.overlay {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 99;
  opacity: 0;
  visibility: hidden;
}

.lock {
  overflow: hidden;
}

.burger {
  width: 40px;
  height: 20px;
  cursor: pointer;
  background-color: transparent;
  border: none;
  position: relative;
  display: none;
}

.burger::before,
.burger::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 3px;
  background-color: var(--color-white);
}

.burger::after {
  top: auto;
  bottom: 0;
}

.burger span {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  height: 3px;
  display: inline-block;
  background-color: var(--color-white);
}

@media (max-width: 991px) {
  .header__bottom {
    display: flex;
    justify-content: flex-end;
  }
  .burger {
    display: block;
  }
  .nav {
    position: fixed;
    left: 0;
    top: 0;
    max-width: 400px;
    width: 80%;
    height: 100vh;
    z-index: 100;
    box-shadow: 5px 0 15px 5px rgba(0, 0, 0, 0.8);
    background-color: var(--color-header);
    transform: translateX(-150vw);
  }
  .nav__list {
    flex-direction: column;
    align-items: flex-start;
    padding: 0;
    padding-top: 10%;
  }
  .nav__item {
    border-bottom: 1px solid var(--color-accent);
    width: 100%;
    text-align-last: left;
  }
  .nav__link {
    padding: 15px 0;
    padding-left: 15px;
    color: var(--color-white);
  }
  .nav__list.transformation {
    transform: translateX(-100%);
    transition: all 0.3s;
  }
  .nav.open {
    transform: translateX(0);
    transition: all 0.3s;
  }
  .overlay.open {
    transition: all 0.3s;
    opacity: 1;
    visibility: visible;
  }
}

.hero {
  position: relative;
  height: 100vh;
  background-image: url("../img/gallery-2.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  margin-bottom: 40px;
}

.hero .swiper-slide {
  height: 100vh;
}

.pluses {
  margin-bottom: 60px;
}

.pluses__title {
  padding: 0;
  margin: 0;
  margin-bottom: 40px;
  margin-left: 20px;
}

.pluses__grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
  grid-column-gap: 32px;
  grid-row-gap: 40px;
}

.pluses__item {
  min-height: 300px;
  padding: 30px 20px;
  box-shadow: 1px 5px 8px rgba(0, 0, 0, 0.1);
}

.item__title {
  margin-bottom: 40px;
  font-size: 24px;
}

.item__text {
  max-width: 130px;
}

.gallery {
  background-color: #f1f1f1;
  padding: 40px 0;
}

.gallery__title {
  margin: 0px 0px 20px 40px;
}

.gallery__text {
  padding: 0 35px;
  line-height: 21px;
  margin-bottom: 40px;
}

.grid {
  padding: 15px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  grid-template-rows: repeat(auto-fit, 200px);
  grid-gap: 20px;
}

.grid__item {
  height: 100%;
}

.grid__item a img {
  display: block;
  height: 100%;
  width: 100%;
  max-height: 200px;
  object-fit: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.grid__item a::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  opacity: 1;
}

.grid__item a:hover:before,
.grid__item a:hover:after {
  opacity: 0;
  transition: 0.3s;
}

.grid__item {
  min-width: 260px;
  position: relative;
  box-shadow: 1px 5px 8px rgba(0, 0, 0, 0.5);
}

.grid__item:hover {
  transform: scale(1.05);
  transition: all 0.3s;
}

@media (max-width: 599px) {
  .grid__item:nth-child(n + 4) {
    display: none;
  }
}

.accessory {
  padding: 40px 0;
}

.tabs {
  font-size: 0;
  margin-left: auto;
  margin-right: auto;
}

.tabs > input[type="radio"] {
  display: none;
}

.tabs > div {
  /* скрыть контент по умолчанию */
  display: none;
  border: 1px solid #e0e0e0;
  padding: 30px 15px;
  font-size: 14px;
}

/* отобразить контент, связанный с вабранной радиокнопкой (input type="radio") */
#tab-btn-1:checked ~ #content-1,
#tab-btn-2:checked ~ #content-2,
#tab-btn-3:checked ~ #content-3 {
  display: grid;
  grid-template-columns: 1fr 1fr 2fr;
  column-gap: 40px;
  line-height: 1.2;
}

.tabs > label {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  user-select: none;
  background-color: #f5f5f5;
  border: 1px solid #e0e0e0;
  padding: 8px 25px;
  font-size: 16px;
  line-height: 1.5;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out;
  cursor: pointer;
  position: relative;
  top: 1px;
}

.tabs > label:hover {
  color: var(--color-accent);
  transition: all 0.3s;
}

.tabs > label:not(:first-of-type) {
  border-left: none;
}

.tabs > input[type="radio"]:checked + label {
  background-color: #fff;
  border-bottom: 1px solid #fff;
}

.accessory__title {
  margin-bottom: 40px;
}

.tab__col-1 {
  text-align: center;
  padding-bottom: 40px;
}

.tab__img {
  margin-bottom: 15px;
}

.tab__info {
  margin-bottom: 15px;
}

.tab__price {
  font-size: 28px;
  font-weight: 700;
  color: var(--color-accent);
}

.tab__col-2 {
  padding-bottom: 40px;
}

.tab__col-2 ul li:not(:last-child) {
  margin-bottom: 15px;
}

.tab__col-2 ul li {
  position: relative;
  line-height: 1.5;
}

.tab__col-2 ul li:before {
  content: "";
  position: absolute;
  top: 5px;
  left: -16px;
  height: 10px;
  width: 10px;
  background-color: var(--color-accent);
}

.tab__col-3 {
  padding-bottom: 40px;
}

.tab__title {
  text-transform: uppercase;
  font-size: 32px;
  color: var(--color-accent);
  margin-bottom: 20px;
}

.tab__text {
  line-height: 1.5;
  margin-bottom: 25px;
}

.tab__warranty {
  display: flex;
  padding: 10px 15px;
  border: 2px solid var(--color-accent);
}

.tab__warranty span {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 18px;
  min-width: 250px;
}

.tab .warranty__img svg {
  width: 60px;
  height: 60px;
  margin-right: 15px;
  fill: var(--color-accent);
}

@media (max-width: 1199px) {
  #tab-btn-1:checked ~ #content-1,
  #tab-btn-2:checked ~ #content-2,
  #tab-btn-3:checked ~ #content-3 {
    display: flex;
    flex-wrap: wrap;
  }
  .tab__col-1 {
    flex: 1 1 320px;
  }
  .tab__col-2 {
    flex: 1 1 280px;
  }
  .tab__title {
    text-align: center;
  }
}

@media (max-width: 767px) {
  .tab__col-2 {
    margin-left: 15px;
  }
}

@media (max-width: 576px) {
  .tabs > label {
    display: inline-block;
    text-align: center;
    vertical-align: middle;
    user-select: none;
    background-color: #f5f5f5;
    border: 1px solid #e0e0e0;
    padding: 5px 10.1px;
    font-size: 16px;
    line-height: 1.5;
    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out;
    cursor: pointer;
    position: relative;
    top: 1px;
  }
  .tabs > label:hover {
    color: var(--color-accent);
    transition: all 0.3s;
  }
}

.footer__container {
  position: relative;
}

.contacts {
  position: absolute;
  top: 134px;
  left: 25px;
  padding: 35px 25px;
  max-width: 320px;
  background-color: var(--color-white);
}

.contacts__title {
  margin-bottom: 40px;
  font-size: 24px;
}

.contacts__list {
  display: flex;
  flex-direction: column;
  row-gap: 15px;
  font-size: 13px;
  margin-bottom: 20px;
}

.contacts__item {
  transition: all 0.3s ease-in-out;
  color: var(--color-subtext);
  line-height: 1.3;
  position: relative;
}

.contacts__item a {
  color: var(--color-subtext);
  transition: all 0.3s ease-in-out;
}

.contacts__item a:hover {
  color: var(--color-accent);
}

.contacts__item span {
  color: var(--color-text);
  text-transform: uppercase;
  font-weight: 700;
  transform: scale(1.5);
}

.contacts__item::before {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  content: "";
  background-image: url("img/mail.svg");
  background-size: 28px 28px;
  height: 28px;
  width: 28px;
}

.footer {
  padding-top: 40px;
  background-color: #f1f1f1;
}

.footer__bottom {
  height: 85px;
  background-color: var(--color-header);
}

.footer__bottom-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 85px;
}

.footer__law {
  max-width: 200px;
  font-size: 10px;
}

.footer__btn {
  padding: 0;
  border: none;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  padding: 5px 12px;
  border: 2px solid var(--color-black);
}

.footer__btn:hover {
  color: var(--color-accent);
  border: 2px solid var(--color-accent);
  transition: all 0.3s;
}
